import React, { Component } from "react";
import * as theme from "../styles/theme";
import {
  ButtonRentalStyled,
  TitleRentalButtonStyled
} from "../styles/component";

class VehicleFilter extends Component {
  render() {
    return (
      <div
        style={{
          marginLeft: "70px",
          marginRight: "30px",
          backgroundColor: "#ffdc2c",
          padding: "5px 10px"
        }}
      >
        <theme.ContainerFlex style={{ flexFlow: "column" }}>
          Local de retirada
          <input type="text" />
          <span>
            <label>
              <input type="checkbox" /> Devolver o carro em outro local Local de
              devolução
            </label>
          </span>
          <input type="text" />
          Retirada
          <input type="text" />
          Devolução
          <input type="text" />
        </theme.ContainerFlex>
        <div>
          <div>Economize até 35%</div>
          <div>
            <label>
              <input type="checkbox" /> Incluir voo{" "}
            </label>
            <label>
              <input type="checkbox" />
              Incluir hotel
            </label>
          </div>
        </div>
        <div>
          <ButtonRentalStyled style={{width:"100%"}}>
            <TitleRentalButtonStyled>Search</TitleRentalButtonStyled>
          </ButtonRentalStyled>
        </div>
      </div>
    );
  }
}

export default VehicleFilter;



